<template>
  <v-card style="width: 690px">
    <v-toolbar
      color="primary"
      dark
      flat
    >
      <v-text-field
        id="paste"
        append-icon=""
        class="mx-2"
        flat
        hide-details
        label="Summoner ID"
        prepend-inner-icon="search"
        solo-inverted
        v-model="inputSummonerID"
        @paste="onPaste"
        @keyup.enter="onClickSearchButton"
      ></v-text-field>
      
      <v-btn 
      small 
      color="primary lighten-2"
      @click="onClickSearchButton"
      >
      Search
      </v-btn>

    </v-toolbar>
  </v-card>
</template>

<script>
export default {
  name: "IndexSearchBar",
  data() {
    return {
      inputSummonerID: '',  //  3 ~ 8  * 2
    }
  },
  methods: {
    onClickSearchButton() {
      this.parseInputSummonerID()
      console.log('axios', this.searchSummernerIDs)

      // nav search bar  
      this.$store.commit('toggleNavSearch', false)
    },
    parseInputSummonerID() {
      //         Array split
      //  op.gg       
      let tmpSearchSummernerIDs = this.inputSummonerID.replace(/(\n|\r\n)/g, ',').split(',')

      // //    Array
      // searchSummernerID = this.inputSummonerID.split('\n')

      //  
      const regExp = /[{}[\]/?.,;:|)*~`!^-_+<>@#$%&\\=('"]/gi;

      //   
      const trashInput = /  /

      //  Array 
      tmpSearchSummernerIDs.forEach((ID, idx) => {
      //  
        if (regExp.test(ID)) {  //  
          ID = ID.replace(regExp, "")  //   
        }
        // console.log(ID, ' ')

      //   
        if (trashInput.test(ID)) {
            ID = ID.replace(trashInput, "")
          }
        // console.log(ID, '')
        
      // ()  ()  
        ID = ID.trim()
        tmpSearchSummernerIDs[idx] = ID
        // console.log(ID + '')
      })

      this.searchSummernerIDs = tmpSearchSummernerIDs
      this.$store.commit('changeSearchSummonerIDs', tmpSearchSummernerIDs)
      console.log(1)
      // console.log(this.searchSummernerIDs)
      // console.log(tmpSearchSummernerIDs)
      this.$router.push('/Profile/'+this.searchSummernerIDs);
    },
    onPaste (e) {
        var clipboardData, pastedData;

        //       (propagation, bubble up) 
        e.stopPropagation();

        //        
        e.preventDefault();

        // Get pasted data via clipboard API
        clipboardData = e.clipboardData || window.clipboardData;
        pastedData = clipboardData.getData('Text');

        // inputSummonerID    
        this.inputSummonerID = pastedData
        //   parse
        this.parseInputSummonerID()
        //    input  
        console.log(2)

        console.log(this.inputSummonerID)
        this.inputSummonerID = this.$store.state.searchSummonerIDs.join(', ')
        console.log(3)
        console.log(this.inputSummonerID)
    },
  },
}
</script>

